<template>
  <div class="home">
    <h2>首页</h2>
    <div class="home-round">
      <section>
        <!--      旋转木马效果-->
        <div><a href="#"></a></div>
        <div><a href="#"></a></div>
        <div><a href="#"></a></div>
        <div><a href="#"></a></div>
        <div><a href="#"></a></div>
        <div><a href="#"></a></div>
      </section>
    </div>
    <div class="home-content">
      <h3>本站使用技术及特色</h3>
      <ol>
        <li>通过@vue/cli脚手架构建（拉取vue-cli2模板）</li>
        <li>使用flex弹性布局</li>
        <li>组件化思想</li>
        <li>父组件传递参数给子组件props</li>
        <li>使用vue-router</li>
        <li>部分组件的路由懒加载</li>
        <li>部分组件内容的浮动布局</li>
        <li>鼠标点击特效</li>
        <li>vue实现轮播图组件</li>
        <li>首页3d转换+css动画实现旋转木马特效及鼠标悬停</li>
        <li>使用透明边框解决首页旋转木马在火狐出现锯齿问题</li>
        <li>使用axios异步获取博客数据</li>
        <li>使用Bomb后端云数据库<a href="https://www.bmob.cn" target="_blank">https://www.bmob.cn</a>存储博客数据</li>
        <li>项目部署到github服务器<a href="https://hjzhao97.github.io" target="_blank">https://hjzhao97.github.io</a></li>
        <li>搭建搜索结果页面，并完善多次搜索时组件得到刷新</li>
        <li>完善重复点击同一组件时页面不刷新的问题</li>
        <li>完善同一组件刷新时，轮播图定时器出现的混乱问题</li>
      </ol>
      <br />
      <h3>待完善的目标</h3>
      <ol>
        <li>内容淡入淡出</li>
        <li>鼠标点击特效的淡出效果</li>
        <li>ajax从后端获取博客数据</li>
        <li>完善站内搜索结果的组件化显示</li>
        <li>媒体查询@media实现手机端页面</li>
        <li>使用iconfont字体图标完善界面</li>
      </ol>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  inject:["routerRefresh"],
  beforeRouteUpdate(to, from, next) {
    this.routerRefresh(); //路由销毁重建方法
    next();
  },
}
</script>

<style scoped>
.home {
  display: block;
}
.home>h2 {
  margin-bottom: 10px;
}
.home .home-round {
  perspective: 800px;
  height: 200px;
  background-color: #eeeeee;
  border-radius: 20px;
}
.home .home-round section {
  position: relative;
  width: 180px;
  height: 100px;
  top: 50px;
  margin: 0 auto;
  transform-style: preserve-3d;
  animation: rotate 16s linear infinite;
}
.home .home-round section:hover {
  animation-play-state: paused;
}
@keyframes rotate {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(360deg);
  }
}
.home .home-round section div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../static/cq.jpg) no-repeat;
  background-size: 100% 100%;
  border: 1px solid transparent;
}
.home .home-round section div:nth-child(1) {
  transform: translateZ(200px);
}
.home .home-round section div:nth-child(2) {
  transform: rotateY(60deg) translateZ(200px);
}
.home .home-round section div:nth-child(3) {
  transform: rotateY(120deg) translateZ(200px);
}
.home .home-round section div:nth-child(4) {
  transform: rotateY(180deg) translateZ(200px);
}
.home .home-round section div:nth-child(5) {
  transform: rotateY(240deg) translateZ(200px);
}
.home .home-round section div:nth-child(6) {
  transform: rotateY(300deg) translateZ(200px);
}
.home .home-round section div a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.home .home-content {
  margin-top: 10px;
  padding: 20px;
  background-color: #eee;
  border-radius: 25px;
}
.home .home-content ol li {
  list-style-position: inside;
}
</style>



